import React from "react";
import { Button, Segment } from "semantic-ui-react";
import * as utils from '../../utils'


const Account = ({ id, tokens, mmr }) => (
    <Segment.Group horizontal >
        <Segment>id: {id}</Segment>
        <Segment>Tokens: {tokens}/60</Segment>
        <Segment>mmr: {mmr}</Segment>
        <Segment>
            <Button content={'take account'}
                onClick={() => utils.takeAccountForTokens(id)} />
        </Segment>
    </Segment.Group>
)

export default Account