import React, { useEffect, useState } from "react";
import { Button, Grid, Segment } from "semantic-ui-react";
import * as utils from '../../utils'


const Order = ({
    active,
    amount,
    beginTokens,
    endTokens,
    orderId,
}) => (
    <Segment color='blue'
        style={{
            width: '80%',
            marginLeft: '10%'
        }} >
        <Grid>
            <Grid.Row divided columns={4}>
                <Grid.Column textAlign="center" verticalAlign='middle'>
                    Begin tokens: {beginTokens}
                </Grid.Column>
                <Grid.Column textAlign="center" verticalAlign='middle'>
                    Now tokens: {endTokens}
                </Grid.Column>
                <Grid.Column textAlign='center' verticalAlign='middle'>
                    Amount: {Math.round(amount*10000)/10000}$
                </Grid.Column>
                <Grid.Column textAlign="center" verticalAlign='middle'>
                    {active ?
                        <Button content={'Update info'}
                            onClick={() => utils.getDataForUpdateOrderData(orderId) } />
                        :
                        <>State: finished</>
                    }
                </Grid.Column>
            </Grid.Row>
        </Grid>
    </Segment>
)


export default Order