import * as api from '../api'
import config from '../config'


export const takeAccount = (id) => {
    const token = localStorage.getItem('token')
    api.takeAcccount(token, id, (err, response) => {
        if (err) console.log(err);
        const { login, password, trophyLevel, rating, behavior } = response.data
        console.log(login, password, trophyLevel, rating, behavior);
        if (response)
            location.replace(config.frontend_url + config.pages.updateAccount +
                `?login=${login}` +
                `&password=${password}` +
                `&id=${id}` +
                `&trophyLevel=${trophyLevel}` +
                `&behavior=${behavior}` +
                `&rating=${rating}`)
    })
}

export const takeAccountForTokens = (id) => {
    const token = localStorage.getItem('token')
    api.takeAcccountForTokens(token, id, (err, response) => {
        if (err) throw err
        const { login,
            password,
            trophyLevel,
            rating,
            behavior,
            tokens,
            orderId } = response.data
        if (response)
            location.replace(config.frontend_url + config.pages.tokenOrder +
                `?login=${login}` +
                `&password=${password}` +
                `&id=${id}` +
                `&trophyLevel=${trophyLevel}` +
                `&behavior=${behavior}` +
                `&rating=${rating}` +
                `&orderId=${orderId}`)
    })
}

export const getQueryParam = (href, key) => {
    if (href.split('?').length == 2) {
        const afterQ = href.split('?')[1]
        const byAnd = afterQ.split('&')
        let value
        byAnd.forEach(item => {
            item = item.split('=')
            if (item[0] == key)
                value = item[1]
        })
        return value ?? null
    } else return null
}

export const getDataForUpdateOrderData = (orderId) => {
    console.log(orderId);
    const token = localStorage.getItem('token')
    api.getOrderData(token, orderId, (err, res) => {
        if (err) throw err
        const { login, password } = res.data
        location.replace(config.frontend_url +
            config.pages.tokenOrder +
            `?login=${login}` +
            `&password=${password}` +
            `&orderId=${orderId}`)
    })
}