import React, { useEffect, useState } from "react";
import { Button, Header } from "semantic-ui-react";

import * as api from '../../api'
import config from '../../config'
import Account from "./Account";


const AccountsList = () => {
    const [accounts, setAccounts] = useState([])
    useEffect(() => {
        const token = localStorage.getItem('token')
        api.getAccounts(token, (err, response) => {
            if (err) console.log(err);
            if (response) setAccounts(response.data)
        })
    }, [])

    const returnAccounts = () => {
        const arr = []
        accounts.forEach(account => {
            console.log(account);
            let action
            if (account.tokens <= 9)
                action = 'Farm 16 tokens'
            if (account.accountHaveRecalibration === true)
                action = 'Recalibtare account'
            if (account.ammountOfLpOnAccount !== 0)
                action = 'Remove lp'
            arr.push(
                <Account id={account._id} action={action} key={account._id} />
            )
        })
        return arr
    }

    return (
        <div style={{ width: '80%', marginLeft: '10%' }}>
            <Header textAlign="center" content='Account list' />
            {returnAccounts()}
        </div>
    )
}

export default AccountsList