import React from "react";
import { Grid, Input, Checkbox, Button } from 'semantic-ui-react'


const Visual = ({
    login, password, lvl, setMmr, sendData,
    setLvl, setBehavior, setAccountPlayable,
    setAmmountOfLp, mmr, behavior, ban,
    ammountOfLp, accountPlayable, setBan,
    tokens, setTokens, message,
    accountWasRecalibrated, setAccountWasRecalibrated
}) => {
    return (
        <div style={{ width: '60%', marginLeft: '20%', fontSize: '15px' }}>
            <Grid>
                <Grid.Row columns={2} textAlign="center">
                    <Grid.Column>
                        login: {login}
                    </Grid.Column>
                    <Grid.Column>
                        password: {password}
                    </Grid.Column>
                </Grid.Row>
                <Grid.Row columns={3} textAlign="center">
                    <Grid.Column>
                        <Input value={mmr} placeholder='mmr on account'
                            onChange={(e) => setMmr(e.target.value)} />
                    </Grid.Column>
                    <Grid.Column>
                        <Input value={lvl} placeholder='account lvl'
                            onChange={(e) => setLvl(e.target.value)} />
                    </Grid.Column>
                    <Grid.Column>
                        <Input value={behavior} placeholder='behavior'
                            onChange={(e) => setBehavior(e.target.value)} />
                    </Grid.Column>
                </Grid.Row>
                <Grid.Row columns={3} textAlign="center">
                    <Grid.Column>
                        <Input value={ammountOfLp} placeholder='ammount of lp'
                            onChange={(e) => setAmmountOfLp(e.target.value)} />
                    </Grid.Column>
                    <Grid.Column>
                        <Checkbox label='is banned' checked={ban}
                            onChange={(e, w) => setBan(w.checked)} />
                    </Grid.Column>
                    <Grid.Column>
                        <Checkbox label='account playable' checked={accountPlayable}
                            onChange={(e, w) => setAccountPlayable(w.checked)} />
                    </Grid.Column>
                </Grid.Row>
                <Grid.Row columns={3} textAlign="center">
                    <Grid.Column>
                        <Input value={tokens} placeholder='tokens'
                            onChange={(e) => setTokens(e.target.value)} />
                    </Grid.Column>
                    <Grid.Column>
                        <Button content='send data' onClick={sendData} />
                    </Grid.Column>
                    <Grid.Column>
                        <Checkbox label='account was recalibrated' checked={accountWasRecalibrated}
                            onChange={(e, w) => setAccountWasRecalibrated(w.checked)} />
                    </Grid.Column>
                </Grid.Row>
                {message === '' ? null :
                    <Grid.Row>
                        <Grid.Column>
                            server answer: {message}
                        </Grid.Column>
                    </Grid.Row>
                }
            </Grid>
        </div>
    )
}

export default Visual