export const sendNewAccountData = (
    _id, dirname, token, mmr, behavior,
    lvl, amountOfLP, isBan,
    isAccountReadyToPlay, tokens,
    accountWasRecalibrated, callback,
) => {
    axios({
        method: 'POST',
        url: backend_url + config.api.sendNewData,
        data: {
            mmr, behavior, lvl, amountOfLP,
            isAccountReadyToPlay, isBan, _id,
            tokens, accountWasRecalibrated, dirname
        },
        headers: { 'Authorization': 'Bearer ' + token }
    })
        .then(res => { callback(false, res) })
        .catch(err => { callback(err, false) })
}

