import React, { useEffect, useState } from "react";
import * as api from '../../api'
import { Header } from "semantic-ui-react";
import Account from "./Account.jsx";


const TokensOrders = () => {
    const [accounts, setAccounts] = useState([])
    const token = localStorage.getItem('token')
    useEffect(() => {
        api.getTokensOrders(token, (err, res) => {
            if (err) throw err

            console.log('get tokens res.data: ', res);
            setAccounts(res)
        })
    }, [])
    return (
        <div style={{ width: '80%', marginLeft: '10%' }}>
            <Header textAlign="center" content='Account list' />
            <AccountsList accounts={accounts} />
        </div>
    )
}

const AccountsList = ({ accounts }) =>
    accounts.map(account => {
        const { tokens, _id, mmr } = account
        return <Account tokens={tokens} id={_id} mmr={mmr}
        key={_id} />
    })

export default TokensOrders