    const lastBehavior = utils.getQueryParam(location.href, 'behavior')
    const lastTrophyLevel = utils.getQueryParam(location.href, 'trophyLevel')

    const [mmr, setMmr] = useState(lastRating)
    const [lvl, setLvl] = useState(lastTrophyLevel)
    const [behavior, setBehavior] = useState(lastBehavior)
