import React, { useState } from "react";
import { Grid, Segment, Image, Button, Input, Checkbox } from 'semantic-ui-react'
import config from '../../config'
import * as api from '../../api'


const Account = ({ path }) => {
    const [rating, setRating] = useState('')
    const [trophylvl, setTrophylvl] = useState('')
    const [behavior, setBehavior] = useState('')
    const [tokens, setTokens] = useState('')

    const [lp, setLp] = useState('')
    const [banned, setBanned] = useState(false)
    const [playable, setPlayable] = useState(true)
    const [message, setMessage] = useState('')

    const [type, setType] = useState('rating')

    const src = config.backend_url
        + '/unchecked-accounts/' + path + `/${type}.jpg`

    return (
        <Segment color="blue" style={{ margin: '1.4em' }}>
            <Grid>
                <Grid.Row columns={1}>
                    <Grid.Column>
                        <Image src={src} />
                    </Grid.Column>
                </Grid.Row>
                <Grid.Row columns={4}>
                    <Grid.Column textAlign="center">
                        <Button
                            onClick={() => setType('rating')}
                            content={'Rating'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Button
                            onClick={() => setType('tokens')}
                            content={'Tokens'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Button
                            onClick={() => setType('last_games')}
                            content={'Behaviour + Last Games'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Button
                            onClick={() => setType('profile_scr')}
                            content={'Trophy lvl'}
                        />
                    </Grid.Column>
                </Grid.Row>

                <Grid.Row columns={4}>
                    <Grid.Column textAlign="center">
                        <Input
                            value={rating}
                            onChange={(e) => setRating(e.target.value)}
                            placeholder={'Rating'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Input
                            value={tokens}
                            onChange={(e) => setTokens(e.target.value)}
                            placeholder={'Tokens'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Input
                            value={behavior}
                            onChange={(e) => {
                                setBehavior(e.target.value)
                                setMessage(e.target.value < 300 ? 'Behaviour is less than 300!' : '')
                            }}
                            placeholder={'Behavior'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Input
                            value={trophylvl}
                            onChange={(e) => {
                                setTrophylvl(e.target.value)
                                setMessage(e.target.value > 300 ? 'Trophy is more than 300!' : '')
                            }}
                            placeholder={'Level'}
                        />
                    </Grid.Column>
                </Grid.Row>

                <Grid.Row columns={4}>
                    <Grid.Column textAlign="center">
                        <Input
                            value={lp}
                            onChange={(e) => setLp(e.target.value)}
                            placeholder={'Lp amount'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Checkbox
                            checked={banned}
                            onChange={(e, w) => setBanned(w.checked)}
                            label={'Banned'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Checkbox
                            checked={playable}
                            onChange={(e, w) => setPlayable(w.checked)}
                            label={'Account playable'}
                        />
                    </Grid.Column>
                    <Grid.Column textAlign="center">
                        <Button
                            onClick={() => api.sendNewAccountData(
                                path.split('_')[0],
                                path,
                                localStorage.getItem('token'),
                                rating,
                                behavior,
                                trophylvl,
                                lp,
                                banned,
                                playable,
                                tokens,
                                false,
                                (err, res) => {
                                    if (err) throw err
                                    setMessage(res.statusText)
                                    setTimeout(() => location.reload(), 2000)
                                }
                            )}
                            content={'Send data'}
                        />
                    </Grid.Column>
                </Grid.Row>
                {message === '' ? null :
                    <Grid.Row columns={1}>
                        <Grid.Column textAlign="center">
                            Response from server: {message}
                        </Grid.Column>
                    </Grid.Row>
                }
            </Grid>
        </Segment>
    )
}

export default Account
