import React, { Suspense } from 'react'
import {
    HashRouter as Router,
    Route, Switch
} from "react-router-dom";

import config from './config'
import Home from './pages/Home'
import AccountsList from './pages/AccountsList'
import CheckAccount from './pages/CheckAccount'
import AddFile from './pages/AddFile'
import ModerateAccount from './pages/ModerateAccount';
import TokensOrders from './pages/TokensOrders'
import TokenOrder from './pages/TokenOrder'
import PersonalSpace from './pages/PersonalSpace'
import Footer from './components/Footer'
import Header from './components/Header'
import NotFound from './components/NotFound'
import LoadingPage from './components/LoadingPage'

const App = () => {
    return (
        <Router>
            <Suspense fallback={<LoadingPage />}>
                <Header />
                <Switch>
                    <Route exact path={config.pages.home} component={Home} />
                    <Route exact path={config.pages.allAccounts} component={AccountsList} />
                    <Route exact path={config.pages.addFile} component={AddFile} />
                    <Route exact path={config.pages.updateAccount} component={CheckAccount} />
                    <Route exact path={config.pages.moderateAccount} component={ModerateAccount} />
                    <Route exact path={config.pages.tokensOrders} component={TokensOrders} />
                    <Route exact path={config.pages.tokenOrder} component={TokenOrder} />
                    <Route exact path={config.pages.PersonalSpace} component={PersonalSpace} />
                    <Route component={NotFound} />
                </Switch>
                {/* <Footer /> */}
            </Suspense>
        </Router>
    )
}

export default App