import React, { useState } from "react";
import { Button, Checkbox, Grid, Input } from "semantic-ui-react";
import * as api from '../../api'
import * as utils from '../../utils'


const TokenOrders = () => {
    const orderId = utils.getQueryParam(window.location.href, 'orderId')
    const login = utils.getQueryParam(window.location.href, 'login')
    const password = utils.getQueryParam(window.location.href, 'password')
    const [endTokens, setNewTokens] = useState('')
    const [endRating, setNewRating] = useState('')
    const [endedOrder, setOrderFinished] = useState(false)
    const [message, setMessage] = useState('')

    //useEffect(() => { }, [])

    const sendData = () => {
        const token = localStorage.getItem('token')
        const data = {
            orderId,
            endTokens,
            endRating,
            endedOrder,
        }
        api.updateTokensOrderData(data, token, (err, response) => {
            if (err) throw err
            setMessage('send successfully, response: ' + response.statusText)
        })
    }

    return (
        <Grid style={{ marginTop: '2em', width: '80%', marginLeft: '10%' }}>
            <Grid.Row columns={2}>
                <Grid.Column textAlign="center">
                    login: <br /> {login}
                </Grid.Column>
                <Grid.Column textAlign="center">
                    password: <br /> {password}
                </Grid.Column>
            </Grid.Row>
            <Grid.Row columns={3}>
                <Grid.Column textAlign="center">
                    <Input value={endTokens}
                        placeholder={'New tokens'}
                        onChange={(e) => setNewTokens(e.target.value)} />
                </Grid.Column>
                <Grid.Column textAlign="center">
                    <Input value={endRating}
                        placeholder={'New rating'}
                        onChange={(e) => setNewRating(e.target.value)} />
                </Grid.Column>
                <Grid.Column textAlign="center">
                    <Checkbox checked={endedOrder}
                        onChange={(a, e) => setOrderFinished(e.checked)}
                        label={'Order finished'} />
                </Grid.Column>
            </Grid.Row>
            <Grid.Row columns={1}>
                <Grid.Column textAlign="center">
                    <Button onClick={sendData}
                        content={'Send New Data'} />
                </Grid.Column>
            </Grid.Row>
            {message === '' ?
                null :
                <Grid.Row columns={1}>
                    <Grid.Column textAlign="center">
                        {message}
                    </Grid.Column>
                </Grid.Row>
            }
        </Grid>
    )
}

export default TokenOrders