import React, { useEffect, useState } from "react";
import { Header } from "semantic-ui-react";
import * as api from '../../api'

import Order from './Order.jsx'


const PersonalSpace = () => {
    const [orders, setOrders] = useState([])

    useEffect(() => {
        const token = localStorage.getItem('token')
        api.getUserOrders(token, (err, responseOrders) => {
            if (err) throw err
            console.log('orders: ', responseOrders);
            setOrders(responseOrders)
        })
    }, [])

    return (
        <>
            <Header content={'Tokens orders'}
                textAlign='center' />
            <OrdersList orders={orders} />
        </>
    )
}

const OrdersList = ({ orders }) => (
    orders.map(order => {
        const { active, amount, beginTokens, endTokens, _id, accountId } = order
        return <Order
            active={active}
            amount={amount}
            beginTokens={beginTokens}
            endTokens={endTokens}
            orderId={_id}
            key={_id}
            accountId={accountId}
        />
    })
)

export default PersonalSpace