import React, { useState } from "react";
import axios from "axios";
import config from "../../config";


const encodeImageFileAsURL = (element) => {
    return new Promise((resolve, reject) => {
        var file = element.files[0];
        if (!file)
            reject('no file')
        var reader = new FileReader();
        reader.onloadend = function () {
            resolve(reader.result)
        }
        reader.readAsDataURL(file);
    })
}

const sendData = async (path = '', token) => {
    const file_input = document.getElementById('file_input')
    const imageBase64 = await encodeImageFileAsURL(file_input)
    axios({
        method: 'post',
        url: config.backend_url + '/add-image-to-static',
        data: {
            imageBase64,
            filename: file_input.value.split("fakepath")[1].slice(1),
            filetype: file_input.value.split('.')[1],
            pathToFile: path
        },
        headers: {Authorization: `Bearer ${token}`}
    }).then(res => { console.log(res); })
}

const AddFile = () => {
    const [filePath, setFilePath] = useState('')

    return (
        <div style={{ margin: '40px' }}>
            <input type="file" id="file_input" />
            <br />
            <input
                value={filePath}
                onChange={(e) => { setFilePath(e.target.value) }}
                style={{ marginTop: '10px' }}
                id="file_input"
                placeholder="path" />
            <br />
            <button
                style={{ marginTop: '10px' }}
                onClick={() => sendData(filePath,
                localStorage.getItem('token'))}>
                Send data
            </button>
        </div>
    )
}

export default AddFile 